[data-tooltip],
[data-tooltip-textarea] {
    position: relative;
}

[data-tooltip]::before,
[data-tooltip-textarea]::before {
    content: "";
    border-left: 5px solid transparent;
    border-right: 5px solid transparent;
    border-bottom: 5px solid #007bff;
    position: absolute;
    transition: all 0.1s ease;
}

[data-tooltip]::before {
    bottom: 20px;
    left: 25px;
}

[data-tooltip-textarea]::before {
    bottom: 121px;
    left: 25px;
}

[data-tooltip]::after,
[data-tooltip-textarea]::after {
    position: absolute;
    padding: 5px 8px;
    background: var(--white);
    color: #007bff;
    border: 1px solid #007bff;
    z-index: 9;
    font-size: 0.8em;
    line-height: 18px;
    font-weight: 700;
    transition: all 0.1s ease;
}

[data-tooltip]::after {
    content: attr(data-tooltip);
    bottom: -10px;
    left: 5px;
}

[data-tooltip-textarea]::after {
    content: attr(data-tooltip-textarea);
    bottom: 91px;
    left: 5px;
}

.toaster {
    box-shadow: 0 10px 6px -6px var(--gray-secondary),
        0 3px 3px var(--gray-secondary-transparent);
